export default "import type { CellPlugin } from '@react-page/editor';\nimport React from 'react';\nimport { Timeline } from 'react-twitter-widgets';\n\ntype Data = {\n  screenName: string;\n  height: number;\n  title: string;\n};\n// you can pass the shape of the data as the generic type argument\nconst customContentPluginTwitter: CellPlugin<Data> = {\n  Renderer: ({ data }) => (\n    <div>\n      <h4>{data.title}</h4>\n      <Timeline\n        dataSource={{\n          sourceType: 'profile',\n          // data has already the right type!\n          screenName: data.screenName,\n        }}\n        options={{\n          height: data.height || 600,\n        }}\n      />\n    </div>\n  ),\n  id: 'twitter-timeline',\n  title: 'Twitter timeline',\n  description: 'A twitter timeline',\n  version: 1,\n  controls: {\n    type: 'autoform',\n    schema: {\n      // this JSONschema is type checked against the generic type argument\n      // the autocompletion of your IDE helps to create this schema\n      properties: {\n        title: {\n          type: 'string',\n          default: 'A Sample Twitter plugin',\n        },\n        screenName: {\n          type: 'string',\n          default: 'typescript',\n        },\n        height: {\n          type: 'number',\n          default: 600,\n        },\n      },\n      required: ['screenName'],\n    },\n  },\n};\n\nexport default customContentPluginTwitter;\n";